*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
 }


.msgcontainer{
    display: flex;
    align-items: center;
    position: fixed;
    bottom: 0px;
    right: 0px;
    width: calc(100% - 25vw);
    border-left: 1px solid #c7c7c7;
}

.chatbody{
    padding-top: 20px;
    height: 100vh;
    background-image: linear-gradient(to right, rgba(0,0,0,0.5),rgba(0,0,0,0.5)), url("https://wallpapercave.com/wp/wp4410728.jpg");
    background-position: center;
    background-repeat: repeat;
    background-attachment: fixed;
    /* background-color: #eff5f9; */
    /* overflow: auto; */
    padding: 65px 15px 10px 15px;
    position: relative;
}


.chatcont{
    width: 75vw;
}

.msgcontainer{
    background-color: white;
}

.msgcontainer input{
    height: 39px;
    border: none;
    outline: none;
    width: 100%;
    padding: 10px;
}

.chatbody .userchat .msgcont{
    margin-left: auto;
}

.chatbody .userchat .img-cont{
    display: none;
}


.chatbody .userchat .msgcontin{
    background-color: #28bebf;
}

.chatbody .receiverchat .msgcontin{
    background-color: #d7e4f4;
    color: #424242;
}


.header{
    /* box-shadow: 0px 0px 5px 5px rgb(0 0 0 / 10%);
     */
    border-bottom: 1px solid gray;
    z-index: 1;
    position: fixed;
    top: 0px;
    background: white;
    height: 60px;
    display: flex;
    justify-content: start;
    align-items: center;
    padding-left: 20px;
    background: #1565c0;
    color: white;
    border-left: 1px solid white;
    width: calc(100vw - 25vw);
    right: 0px;
}

.header h2{
    padding-left: 10px;
}

.header .userprofile{
display: flex;
align-items: center;
}

.mainchatcont{
    display: flex;
}

.mainchatcont>div:nth-child(1){
    width: 25vw;
    position: relative;
    padding-top: 60px;
}

.mainchatcont .sidebartop{
    display: flex;
    align-items: center;
    background-color: #1565c0;
    position: absolute;
    width: 100%;
    left: 0px;
    top: 0px; 
    height: 60px;   
    justify-content: center;
    border-bottom: 1px solid #c7c7c7;
}


.mainchatcont .sidebartop .userprofile img{
   width: 40px;
   height: 40px;
   border-radius: 50%;
}


.mainchatcont .sidebartop .userprofile {
   display: flex; 
   padding-right: 10px;
 }


.mainchatcont .sidebartop input{
    padding: 8px;
    outline: none;
    border-radius: 10px;
    border: none;
    width: 100%;
}

.mainchatcont .usercard{
    background-color: white;
    border-bottom: 1px solid #c7c7c7;
    padding: 10px 15px;
    text-align: left;
    cursor: pointer;
    transition: all 0.3;
    display: flex;
    align-items: center;
    position: relative;
}

.mainchatcont .usercard .lastchattime small{
    position: absolute;
    right: 15px;
    top: 13px;
}

.mainchatcont .usercard img{
    margin-right: 10px;
}

.mainchatcont .usercard .profile{
  display: flex;
}


.mainchatcont .usercard:hover{
    background-color: #d7e4f4;    
}


.mainchatcont .sidebartop{
    padding: 5px 15px;
}
.sidebartop .searchbar{
    width: 100%;
    
}


.chatbodyin{
    max-width: 700px;
    margin: auto;
    max-height: calc(100% - 10vh);
    overflow: auto;
}

.chatbodyin::-webkit-scrollbar{
    display: none;
}